import { makeStyles } from '@material-ui/core/styles';
import { pxToRem } from '../../../../styles/themes/vad/constants';
import get from 'lodash/get';

const useStyles = makeStyles(theme => {
  const { left, marginLR } = theme.mixins;

  return {
    wrapper: {
      background: props =>
        get(
          props,
          'fields.backgroundColor.fields.colorCode.value',
          theme.mixins.White()
        ),
    },
    mainContent: {
      ...marginLR(pxToRem(24), pxToRem(24)),
      padding: 0,
      position: 'relative',
      display: 'flex',
      justifyContent: 'space-between',
      flexDirection: 'row',
      paddingTop: pxToRem(48),
      paddingBottom: pxToRem(48),
      ...theme.shape.componentSpacing,
      [theme.breakpoints.up('lg')]: {
        ...marginLR(pxToRem(81), pxToRem(91)),
        paddingTop: pxToRem(84),
        paddingBottom: pxToRem(88),
        height: '100%',
      },
    },
    heading: {
      width: pxToRem(304),
      [theme.breakpoints.up('lg')]: {
        width: pxToRem(640),
      },
    },
    tabsContainer: {
      '& .MuiTabs-flexContainer': {
        [theme.breakpoints.up('lg')]: {
          width: pxToRem(175),
        },
      },
    },
    tabRoot: {
      '& .MuiTabs-root': {
        marginBottom: theme.spacing(1.5),
        marginTop: theme.spacing(4),
      },
    },
    contentSection: {
      width: '100%',

      [theme.breakpoints.up('lg')]: {
        flexBasis: '53%',
      },
    },
    imageSection: {
      display: 'none',

      [theme.breakpoints.up('lg')]: {
        position: 'relative',
        flexGrow: 0,
        flexBasis: '30%',
        display: 'block',

        '& .image-above': {
          position: 'absolute',
          top: '15%',
          width: '80%',
          height: '70%',
          zIndex: 1,
          ...left('-20%'),
          // Picture component add paddingTop: 125% with respect of image aspect ratio
          // especifically for this component we dont need it to achieve design
          paddingTop: 0,
        },
        '& .image-below': {
          height: '100%',
          width: '100%',
        },
      },
      [theme.breakpoints.up('xl')]: {
        flexBasis: '35%',
        '& .image-above': {
          top: '10%',
          width: '80%',
          height: '80%',
        },
      },
    },
    formWrapper: {
      display: 'flex',
      flexWrap: 'wrap',

      '& fieldset': {
        width: '100%',

        [theme.breakpoints.up('lg')]: {
          width: '45%',
          ...marginLR(0, pxToRem(16)),
        },
        '& .MuiAutocomplete-popperDisablePortal': {
          top: pxToRem(94),
        },
      },
      '& .MuiFormControlLabel-root': {
        ...marginLR(`-${pxToRem(11)}`, pxToRem(16)),
      },
      [theme.breakpoints.up('lg')]: {
        width: 'auto',
      },
    },
    fullWidth: {
      flex: '0 0 100%',
    },
    button: {
      marginTop: pxToRem(32),
    },
    toDropdown: {
      '& .MuiInputBase-root .MuiSelect-select:hover': {
        backgroundColor: 'transparent',
      },
      '& .Mui-disabled': {
        color: theme.mixins.Black(),
      },
      '& .MuiSelect-icon': {
        display: 'none',
      },
    },
  };
});

export default useStyles;
